<template>
	<div :disabled="disabled" v-on="listeners" class="mvi-collapse">
		<slot></slot>
	</div>
</template>

<script>
	export default {
		name:"m-collapse",
		data(){
			return {
				children:[]
			}
		},
		model:{
			prop:'openIndex',
			event:'model-change'
		},
		props:{
			openIndex:{//
				type:[Number,Array],
				default:null
			},
			accordion:{//
				type:Boolean,
				default:false
			},
			inBorder:{//
				type:Boolean,
				default:true
			},
			outBorder:{//
				type:Boolean,
				default:true
			},
			disabled:{//
				type:Boolean,
				default:false
			},
			active:{//
				type:Boolean,
				default:true
			},
			noWrap:{//
				type:Boolean,
				default:false
			},
			openArrow:{//
				type:[String,Object],
				default:'angle-up'
			},
			closeArrow:{//
				type:[String,Object],
				default:'angle-down'
			},
			arrowClass:{//
				type:String,
				default:null
			},
			slide:{//
				type:Boolean,
				default:true
			},
			timeout:{//,ms
				type:Number,
				default:300
			}
		},
		provide(){
			return {
				collapse:this
			};
		},
		computed:{
			listeners(){
				return Object.assign({},this.$listeners)
			}
		},
		methods:{
			
		}
	}
</script>

<style lang="less" scoped>
	@import "../../css/mvi-basic.less";
	
	.mvi-collapse{
		display: block;
		width: 100%;
		vertical-align: middle;
		background-color: #fff;
	}
</style>
