<template>
	<div v-on="listeners" :class="'mvi-collapse-item'+(computedOutBorder?' mvi-collapse-item-border':'')">
		<m-cell class="mvi-collapse-cell" :icon="icon" :content="label" :title="title" :border="cellBorder" :arrow="arrow" 
		:title-class="titleClass" :content-class="labelClass" :icon-class="iconClass" @click="changeCollapse" :active="computedActive" 
		 :disabled="disabled || collapse.disabled" :no-wrap="computedNoWrap" :arrow-class="computedArrowClass"></m-cell>
		<transition :name="(computedSlide?'mvi-collapse':'')" @before-enter="beforeEnter" @after-enter="afterEnter" @before-leave="beforeLeave"
		@afterLeave="afterLeave">
			<div ref="wrapper" v-show="open" class="mvi-collapse-item-wrapper">
				<div class="mvi-collapse-item-content">
					<slot v-if="$slots.default"></slot>
					<span v-else v-text="content" :class="''+(contentClass?' '+contentClass:'')"></span>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
	import $util from "../../util/util"
	export default {
		name: "m-collapse-item",
		created() {
			this.collapse.children.push(this);
		},
		data() {
			return {
				open:true,
				needAnimation:true,
				cellBorder:false
			}
		},
		props: {
			icon: { //
				type: [String, Object],
				default: null
			},
			title: { //
				type: String,
				default: null
			},
			label: { //
				type: String,
				default: null
			},
			outBorder: { //
				type: Boolean,
				default: null
			},
			inBorder: { //
				type: Boolean,
				default: null
			},
			titleClass: { //
				type: String,
				default: null
			},
			labelClass: { //
				type: String,
				default: null
			},
			iconClass: { //
				type: String,
				default: null
			},
			active: { //
				type: Boolean,
				default: null
			},
			disabled: { //
				type: Boolean,
				default: false
			},
			content: { //
				type: String,
				default: ''
			},
			contentClass: { //
				type: String,
				default: null
			},
			noWrap: { //
				type: Boolean,
				default: null
			},
			openArrow: {//
				type: [String, Object],
				default: null
			},
			closeArrow: {//
				type: [String, Object],
				default: null
			},
			arrowClass:{//
				type:String,
				default:null
			},
			slide:{//
				type:Boolean,
				default: null
			},
			timeout:{//,ms
				type:Number,
				default: null
			}
		},
		inject: ['collapse'],
		mounted() {
			this.cellBorder = this.computedInBorder;
			this.setSelfHeight();
			this.needAnimation = false;
			this.isNeedHideSelf();
		},
		watch:{
			'collapse.openIndex':function(newValue,oldValue){
				this.needAnimation = true;
				this.isNeedHideSelf();
			}
		},
		computed: {
			listeners() {
				return Object.assign({}, this.$listeners);
			},
			computedArrowClass(){
				if (typeof(this.arrowClass) == "string" && this.arrowClass) {
					return this.arrowClass;
				} else {
					return this.collapse.arrowClass;
				}
			},
			computedOutBorder() {
				if (typeof(this.outBorder) == "boolean") {
					return this.outBorder;
				} else {
					return this.collapse.outBorder;
				}
			},
			computedInBorder() {
				if (typeof(this.inBorder) == "boolean") {
					return this.inBorder;
				} else {
					return this.collapse.inBorder;
				}
			},
			computedNoWrap() {
				if (typeof(this.noWrap) == "boolean") {
					return this.noWrap;
				} else {
					return this.collapse.noWrap;
				}
			},
			//itemcollapse
			itemIndex() {
				var index = 0;
				for (var i = 0; i < this.collapse.children.length; i++) {
					if (this.collapse.children[i] == this) {
						index = i;
						break;
					}
				}
				return index;
			},
			//
			computedOpenArrow() {
				if ((typeof(this.openArrow) == 'string' && this.openArrow) ||
					$util.isObject(this.openArrow)) {
					return this.openArrow;
				} else {
					return this.collapse.openArrow;
				}
			},
			//
			computedCloseArrow() {
				if ((typeof(this.closeArrow) == 'string' && this.closeArrow) ||
					$util.isObject(this.openArrow)) {
					return this.closeArrow;
				} else {
					return this.collapse.closeArrow;
				}
			},
			//
			arrow() {
				return this.open ? this.computedOpenArrow : this.computedCloseArrow;
			},
			//
			computedActive() {
				if (typeof(this.active) == "boolean") {
					return this.active;
				} else {
					return this.collapse.active;
				}
			},
			//
			computedSlide(){
				if (typeof(this.slide) == "boolean") {
					return this.slide;
				} else {
					return this.collapse.slide;
				}
			},
			//
			computedTimeout(){
				if (typeof(this.timeout) == "boolean") {
					return this.timeout;
				} else {
					return this.collapse.timeout;
				}
			}
		},
		methods: {
			//
			beforeEnter(el){
				if(this.needAnimation && this.computedSlide){
					el.style.transition = 'height ' + this.computedTimeout + 'ms';
					el.style.webkitTransition = 'height ' + this.computedTimeout + 'ms';
				}
				this.cellBorder = this.computedInBorder;
			},
			//
			afterEnter(el){
				if(this.needAnimation && this.computedSlide){
					el.style.transition = '';
					el.style.webkitTransition = '';
				}
			},
			//
			beforeLeave(el){
				if(this.needAnimation && this.computedSlide){
					el.style.transition = 'height ' + this.computedTimeout + 'ms';
					el.style.webkitTransition = 'height ' + this.computedTimeout + 'ms';
				}
			},
			//
			afterLeave(el){
				if(this.needAnimation && this.computedSlide){
					el.style.transition = '';
					el.style.webkitTransition = '';
				}
				this.cellBorder = false;
			},
			//
			setSelfHeight(){
				if(this.$refs.wrapper && this.computedSlide){
					this.$refs.wrapper.style.height = this.$refs.wrapper.offsetHeight + 'px';
				}
			},
			//
			isNeedHideSelf(){
				if (this.collapse.accordion) { //
					if(this.collapse.openIndex == this.itemIndex){
						this.open = true;
					}else{
						this.open = false;
					}
				} else { //
					//
					if ($util.isNumber(this.collapse.openIndex)) {
						if (this.collapse.openIndex == this.itemIndex) { 
							this.open = true;
						} else {
							this.open = false;
						}
					} else if ((this.collapse.openIndex) instanceof Array) { //
						if (this.collapse.openIndex.includes(this.itemIndex)) {
							this.open = true;
						} else {
							this.open = false;
						}
					} else {
						this.open = true;
					}
				}
			},
			//collapse-item
			changeCollapse() {
				if (this.disabled || this.collapse.disabled) {
					return false;
				}
				if (this.collapse.accordion) { //
					//
					if(this.collapse.openIndex == this.itemIndex){
						this.collapse.$emit('update:openIndex', null);
						this.collapse.$emit('model-change', null);
					}else{
						this.collapse.$emit('update:openIndex', this.itemIndex);
						this.collapse.$emit('model-change', this.itemIndex);
					}
				} else { //
					//
					if ($util.isNumber(this.collapse.openIndex)) {
						if (this.collapse.openIndex == this.itemIndex) { //
							this.collapse.$emit('update:openIndex', []);
							this.collapse.$emit('model-change', []);
						} else { //
							this.collapse.$emit('update:openIndex', [this.collapse.openIndex, this.itemIndex]);
							this.collapse.$emit('model-change', [this.collapse.openIndex, this.itemIndex]);
						}
					} else if ((this.collapse.openIndex) instanceof Array) { //
						if (this.collapse.openIndex.includes(this.itemIndex)) { //
							var arry = this.collapse.openIndex;
							var index = this.getIndex(arry, this.itemIndex);
							arry.splice(index, 1);
							this.collapse.$emit('update:openIndex', arry);
							this.collapse.$emit('model-change', arry);
						} else { //
							var arry = this.collapse.openIndex;
							arry.push(this.itemIndex);
							this.collapse.$emit('update:openIndex', arry);
							this.collapse.$emit('model-change', arry);
						}
					} else {
						var arry = [];
						arry.push(this.itemIndex);
						this.collapse.$emit('update:openIndex', arry);
						this.collapse.$emit('model-change', arry);
					}
				}
			},
			//
			getIndex(arry, value) {
				var index = 0;
				arry.forEach((item, i) => {
					if (item == value) {
						index = i;
					}
				})
				return index;
			}
		}
	}
</script>
<style lang="less" scoped>
	@import "../../css/mvi-basic.less";

	.mvi-collapse-item {
		display: block;
		width: 100%;
		overflow: hidden;
		background-color: inherit;
	}

	.mvi-collapse-item.mvi-collapse-item-border {
		border-bottom: 1px solid @border-color;
	}
	
	.mvi-collapse-cell{
		background-color: inherit;
		color: inherit;
		cursor: pointer;
	}
	
	.mvi-collapse-item-wrapper {
		display: block;
		width: 100%;
		overflow: hidden;
	}

	.mvi-collapse-item-content {
		display: block;
		width: 100%;
		padding: @mp-sm;
		font-size: @font-size-default;
		color: inherit;
		line-height: 1.5;
	}
	
	.mvi-collapse-enter,.mvi-collapse-leave-to{
		height: 0 !important;
	}
</style>
