//Number
(function() {
	/**
	 * @param {Object} num
	 * 
	 */
	Number.prototype.multiplication = function(num) {
		var m = 0;
		var s1 = this.toString();
		var s2 = num.toString();
		try {
			m += s1.split(".")[1].length
		} catch (e) {}
		try {
			m += s2.split(".")[1].length
		} catch (e) {}
		return Number(s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m)
	}
	/**
	 * @param {Object} num
	 * 
	 */
	Number.prototype.division = function(num) {
		var t1 = 0;
		var t2 = 0;
		var s1 = this.toString();
		var s2 = num.toString();
		try {
			t1 = s1.split(".")[1].length
		} catch (e) {}
		try {
			t2 = s2.split(".")[1].length
		} catch (e) {}
		s1 = Number(s1.replace(".", ""))
		s2 = Number(s2.replace(".", ""))
		return (s1 / s2) * Math.pow(10, t2 - t1);
	}
	/**
	 * @param {Object} num
	 * 
	 */
	Number.prototype.add = function(num) {
		var r1 = 0;
		var r2 = 0;
		var m = 0;
		try {
			r1 = this.toString().split(".")[1].length
		} catch (e) {}
		try {
			r2 = num.toString().split(".")[1].length
		} catch (e) {}
		m = Math.pow(10, Math.max(r1, r2))
		return (this * m + num * m) / m
	}
	
	/**
	 * @param {Object} num
	 * 
	 */
	Number.prototype.subtraction = function(num){
		var r1 = 0;
		var r2 = 0;
		var m = 0;
		try {
			r1 = this.toString().split(".")[1].length
		} catch (e) {}
		try {
			r2 = num.toString().split(".")[1].length
		} catch (e) {}
		m = Math.pow(10, Math.max(r1, r2))
		return (this * m - num * m) / m
	}
})(window);

//data
(function() {
	"use strict"
	/**
	 * Elementdata
	 * 
	 */
	Element.prototype.data = function(key, value) {
		var _this = this,
			_dataName = "vi", // DOM
			_data = {};
		// ,
		if (typeof key === 'undefined' && typeof value === 'undefined') {
			return _this[_dataName];
		}
		// setter
		if (typeof(value) !== 'undefined') {
			_data = _this[_dataName] || {};
			_data[key] = value;
			_this[_dataName] = _data;
			return this;
		}
		// getter
		else {
			_data = _this[_dataName] || {};
			return _data[key];
		}
	};
	/**
	 * NodeListdata
	 */
	NodeList.prototype.data = function(key, value) {
		// setter
		if (typeof(value) !== 'undefined') {
			[].forEach.call(this, function(element, index) {
				element.data(key, value);
			});
			return this;
		}
		// getter
		else {
			return this[0].data(key, value); // getter 
		}
	};
	/**
	 * HTMLConllectiondata
	 */
	HTMLCollection.prototype.data = function(key, value) {
		// setter
		if (typeof(value) !== 'undefined') {
			[].forEach.call(this, function(element, index) {
				element.data(key, value);
			});
			return this;
		}
		// getter
		else {
			return this[0].data(key, value); // getter 
		}
	};

	/**
	 * forEachNodeList
	 * IENodeListforEachBUG
	 */
	NodeList.prototype.forEach = Array.prototype.forEach;

})(window)
